body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  color: white;
}

.container h1 {
  font-family: Tahoma, Geneva, sans-serif;
}

.container .info {
  display: -webkit-flex;
  display: flex;
  font-size: 90%;
  width: 100%;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.container .info h1 {
  margin-right: 5%;
  cursor: pointer;
}

.container .game {
  display: -webkit-flex;
  display: flex;
  background: rgba(0, 0, 0, .6);
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  color: white;
  height: 100%;
  border: 2px white solid;
  width: 800px;
  margin-bottom: 5%;
}

.container .small {
  padding-top: 8%;
}

.game .grid-container {
  box-sizing: border-box;
  border: 1px solid black;
  margin-bottom: 2%;
}

.game .row {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  box-sizing: border-box;
}

.game .dead-cell {
  width: 20px;
  height: 20px;
  border: 1px solid black;
}

.game .live-cell {
  width: 20px;
  height: 20px;
  border: 1px solid black;
  background: white;
}

.generation-count {
  font-size: 1.5rem;
  font-weight: normal;
}

.controls {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin-top: 3%;
}

.options {
  margin: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 5%;
}

.options h1 {
  margin-bottom: 10%;
}

.options .option-settings {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.options .option-labels {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  font-size: 22px;
}

.options .option-controls {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  margin-bottom: 1%;
}

.options .option-controls .top-options {
  display: -webkit-flex;
  display: flex;
}

.options .option-controls .size-options {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.options .option-controls .size-options p {
  font-size: 22px;
}

.options .option-controls .size-options button {
  cursor: pointer;
}

.options button {
  background: rgba(0, 0, 0, 0);
  color: #fff;
  font-family: Sans-serif;
  font-size: 1.4em;
  height: 50px;
  width: 100px;
  line-height: 50px;
  margin: 25px 25px;
  text-align: center;
  border: 1px white solid;
  transition: all 0.3s ease 0s;
}

.size-options button.selected {
  background: rgba(0, 0, 0, .8);
  color: white;
}

.option-controls {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.option-controls .pattern-options {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
}

.option-controls .pattern-options p {
  font-size: 22px;
}

.options .option-controls .bottom-options {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.options .option-controls .bottom-options p {
  font-size: 22px;
}

.option-controls .bottom-options .grid-color {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.option-controls .bottom-options .cell-color {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.option-controls select {
  width: 50%;
  height: 40px;
  color: white;
  position: relative;
  margin-bottom: 4%;
  background: transparent;
  font-family: Sans-serif;
  font-size: 1.4em;
  width: 150px;
  line-height: 50px;
  margin: 25px 25px;
}

.option-controls select option {
  background: rgba(0, 0, 0, 1);
}

.rules {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  background: rgba(0, 0, 0, .6);
  border: 3px white solid;
  padding: 3%;
  font-size: 1.4rem;
  margin: auto;
  margin-top: 10%;
  margin-bottom: 20%;
}

.rules .closeButton {
  -webkit-align-self: flex-end;
          align-self: flex-end;
}

.about {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  background: rgba(0, 0, 0, .6);
  border: 3px white solid;
  padding: 3%;
  font-size: 1.4rem;
  margin: auto;
  margin-top: 5%;
  margin-bottom: 20%;
  width: 50%;
}

.about .closeButton {
  -webkit-align-self: flex-end;
          align-self: flex-end;
}

@media (max-width: 1024px) {
  .about {
    width: 70%;
  }

  .rules {
    width: 70%;
  }
}

@media (max-width: 850px) {
  .container .game {
    width: 600px;
    margin: auto;
    margin-bottom: 10%;
  }

  .options .option-controls .top-options {
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .options .option-controls .size-options {
    display: none;
  }
  .options .option-controls .pattern-options {
    -webkit-justify-content: center;
            justify-content: center;
  }

  .options .option-controls .bottom-options {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
}

 @media (max-width: 600px) {
  .container .game {
     width: 100%;
     border: none;
     background: none;
     margin-top: -40px;
   }

  .container .game .options {
    width: 100%;
    margin-left: -5%;
  }

  .options .option-controls .pattern-options select {
    margin-left: 16%;
  }

  .about {
    width: 85%;
    margin-top: 0;
  }

  .rules {
    width: 85%;
    margin-top: 0;
  }
 }

.App {
  text-align: center;
  position: static;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.background {
  background-image: url("https://i.pinimg.com/originals/c1/e7/30/c1e730a38f30b04816de325e5fced5f8.jpg");
  top: 0;
  width: 100%;
  height: 100%;
  padding-top: 0;
  border: 2px grey solid;
  position: fixed;
  background-attachment: fixed;
  z-index: -100;
}

